<template>
    <!-- :is-bar      true -->
    <!-- :pull-up :pull-down      -->
    <div class="mi-scroll-parent">
        <mi-scroll  :pull-up="pullUp" :pull-down="pullDown">
            <!--   -->
            <div v-for="i in 20" class="list" slot="content"> item-{{ i }}</div>
            <!--  -->
            <div v-for="item in arrList" class="list"  slot="pullUpStr"> item-{{item.num}}</div>
            <div v-for="item in arrList2" class="list"  slot="pullDownStr"> item-{{item.num}}</div>
        </mi-scroll>
    </div>
</template>
<style>
    .mi-scroll-parent{
        box-sizing: border-box;
        left: 0;
        top:0;
        position: absolute;
        width: 100%;
        height: 100%;
        overflow: hidden;
    }
    .scroll-box{
        border-top: 1px solid #e5e5e5;
    }
    .list {
        background: #ffffff;
        height: 50px;
        line-height: 50px;
        font-size: 16px;
        padding: 0 10px;
        box-sizing: border-box;
        border-bottom: 1px solid #e5e5e5;
    }
</style>
<script>
import {
    MiScroll
} from '../components/index';

export default {
    components: {
        MiScroll
    },
    data() {
        return {
            indexUp:0,
            indexDown:0,
            dNum:0,
            uNum:0,
            arrList:[],
            arrList2:[]
        };
    },
    mounted(){

    },
    methods: {
        pullUp(MiScroll){
            //console.log('UP-AJAX');
            //MiScroll
            //AJAX isUpdateUp  '' //  ing  success  fail
            //isUpData   true

            MiScroll.isUpdateUp='ing';
            //ajax
            setTimeout(()=>{
                this.arrList=[];
                for(var i=0;i<5;i++){
                    this.arrList.push({'num':parseInt(Math.random()*80000)})
                }
                MiScroll.isUpdateUp='success'
                //
                //MiScroll.isUpData=false
            },800)
        },
        pullDown(MiScroll){
            //console.log('DOWN-AJAX');
            //MiScroll
            //AJAX isUpdateDown  '' //  ing  success  fail
            //isDownData   true

            MiScroll.isUpdateDown='ing'
            //ajax
            setTimeout(()=>{
                this.arrList2=[];
                // for(var i=0;i<5;i++){
                //     this.arrList2.push({'num':parseInt(Math.random()*80000)})
                // }
                MiScroll.isUpdateDown='success'
                //
                MiScroll.isDownData=false
            },800)
        }
    }
}
</script>



